<template>
  <div class="">
    <Button 
      label="Add"
      class="mb-4"
      size="small"
      icon="pi pi-plus"
      @click="openModal()"
      v-tooltip.left="`Register Kategori`"
    />
    <Dialog v-model:visible="visible" :modal="true" class="p-fluid" header="Register Kategori" :breakpoints="{ '960px': '80vw' }" :style="{ width: '50vw' }" @hide="hideModal()">
      <div class="" style="">
        <div class="flex flex-column gap-2 mb-2">
          <label for="jenis_kategori">Jenis Kategori</label>
          <Dropdown 
            v-model="dataForm.jenis_kategori" 
            :options="$store.state.data_static.jenis_kategori" 
            optionLabel="text" 
            optionValue="value" 
            placeholder="piilh jenis kategori" 
            class="w-full" 
            :class="{'p-invalid': v$.dataForm.jenis_kategori.$invalid && afterSubmit}" 
          />
        </div>
        <div class="flex flex-column gap-2">
          <label for="nama_kategori">Nama Kategori</label>
          <InputText id="nama_kategori" v-model="dataForm.nama_kategori" aria-describedby="nama_kategori-help" :class="{'p-invalid': v$.dataForm.nama_kategori.$invalid && afterSubmit}"/>
        </div>
      </div>
      <template #footer>
        <div class="flex justify-content-end">
          <Button 
            label="Save"
            class=""
            size="small"
            v-tooltip.left="`save`"
            @click="submit()"
          />
        </div>
      </template>
    </Dialog>
  </div>
</template>

<script>
import { useVuelidate } from '@vuelidate/core'
import { required } from '@vuelidate/validators'
export default {
  emits: [ 'refresh' ],
  components: {
  },
  setup () {
    return { v$: useVuelidate() }
  },
  data() {
    return {
      afterSubmit: false,
      visible: false,
      dataForm: {
        jenis_kategori: null,
        nama_kategori: null,
      },
    };
  },
  computed: {
    isValid() {
      return !this.v$.dataForm.$invalid;
    },
    isDirty() {
      return this.v$.dataForm.$anyDirty;
    },
  },
  validations () {
    return {
      dataForm: {
        jenis_kategori: { required },
        nama_kategori: { required },
      }
    }
  },
  mounted() {
  },
  methods: {
    openModal(){
      const vm = this
      vm.visible = true
      vm.v$.$reset();
    },
    hideModal(){
      const vm = this
      vm.visible = false
      vm.dataForm = {
        jenis_kategori: null,
        nama_kategori: null,
      }
    },
    async submit(){
      const vm = this
      vm.afterSubmit = true
      if(vm.isValid){
        const res = await vm.$axios.post('kategori/register', vm.dataForm)
        if(res.data.status == 200){
          vm.afterSubmit = false
          vm.visible = false
          vm.$emit('refresh')
          vm.$toast.add({ severity: 'success', summary: 'Konfirmasi', detail: 'Berhasil Register Kategori', life: 3000 });
        }else{
          vm.$toast.add({ severity: 'error', summary: 'Konfirmasi', detail: 'Gagal Register Kategori', life: 3000 });
        }
      }else{
        vm.$toast.add({ severity: 'error', summary: 'Konfirmasi', detail: 'Data Tidak Valid', life: 3000 });
      }
    },
  },
};
</script>